import tuiLoadmore from '@/plugins/thorui/components/loadmore/loadmore.vue';
import tuiNomore from '@/plugins/thorui/components/nomore/nomore';
import { comments } from '@/http/api.js';
export default {
	components: {
		tuiLoadmore,
		tuiNomore
	},
	data() {
		return {
			id:'41',
			comments:[],
			loadding: false,
			pullUpOn: true,
			page: 1,
			type:'travels'
		}
	},
	onShow() {
		wx.hideHomeButton()
	},
	onLoad(options) {
		if(options.id){
			this.id = options.id;
		}
		if(options.type){
			this.type = options.type;
		}
		this.getDetail(this.type,this.id)
	},
	methods: {
		getDetail(type,id){
			comments(this.page,type,id).then(res => {
				// this.comments = res.data.data;
				if (!this.pullUpOn) return;
				this.loadding = true;
				if (res.data.data.length == 0) {
					this.loadding = false;
					this.pullUpOn = false;
					return;
				}
				if (res.data.current_page == res.data.last_page) {
					this.loadding = false;
					this.pullUpOn = false;
					this.comments = this.comments.concat(res.data.data);
					this.page = this.page + 1;
					return;
				}
				this.comments = this.comments.concat(res.data.data);
				this.page = this.page + 1;
			});
		},
	},
	// 页面上拉触底事件的处理函数
	onReachBottom: function() {
		this.getDetail(this.type,this.id);
	}
}
